@import '../../sass/variables';


.logo {
    text-decoration: none;

    &__container {
        font-family: "Great Vibes", sans-serif;
        display: flex;
        flex-direction: column;
    }

    &__title {
        position: relative;
        font-family: "Marck Script", sans-serif;
        font-size: 36px;
        margin-bottom: 5px;
        color: $red-main;
    }

    &__slogan {
        font-size: 10px;
        letter-spacing: 3px;
        text-transform: uppercase;
        font-weight: 700;
    }
}

.logo-header {
    &__slogan {
        color: $blue-main;
    }
}

.logo-footer {
    &__slogan {
        color: $grey-main;
    }
}

