@import '../../sass/variables';

.navigation {
    width: 100%;

    &__link {
        text-decoration: none;
        color: $blue-main;
    }

    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__item {
        cursor: pointer;
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
        padding: 17px 0;
   
        &:hover {
            color: $red-main;
        }
    }
}

.navigation-header {
    &__item {
        &_current {
            border-bottom: 2px solid $red-main;
        }
    }
}

.navigation-footer {
    &__item {
        color: $grey-light;
    }
}

@media screen and (max-width: 1200px) {
    .navigation-footer {
        &__list {
            justify-content: space-around;
        }
    }

    .navigation-header {
        &__list {
            justify-content: space-around;
        }
    }
}

@media screen and (max-width: 750px) {
    .navigation-footer {
        &__list {
            flex-direction: column;
        }
    }

    .navigation-header {
        &__list {
            flex-direction: column;
        }
    }
}




