@import '../../sass/variables';

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    margin: 15px;
    background-color: #FFFFFF;
    width: calc(25% - 30px);
    min-height: 384px;
    border-radius: 5px;
    text-decoration: none;
    color: $blue-main;

    &__img {
        height: 188px;
        border-radius: 5px;
    }

    &__top {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 230px;
    }

    &__bottom {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-grow: 1;
    }

    &__btn {
        margin: auto 0 40px;
    }

    &__title {
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 10px;
    }

    &__price {
        font-size: 24px;
        font-weight: 700;
        margin-bottom: 10px;
    }

    &:hover {
        box-shadow: 0 0 10px 5px rgb(221 221 221);
    }
}

@media screen and (max-width: 1200px) {
    .card {
        width: calc(33% - 30px);
        flex: 0 1 calc(33% - 30px);
    }
}

@media screen and (max-width: 750px) {
    .card {
        width: 100%;
        flex: 0 1 100%;
        flex-direction: row;
        justify-content: center;
        min-height: 280px;

        &__bottom {
            flex-grow: 0;
            margin-left: 30px;
        }

        &__btn {
            margin: auto;
        }
    }
}